import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './Post.scss';
import QueueAnim from 'rc-queue-anim';
import CountDown from '../CountDown';
export default class index extends Component {
	
	getTitle = (title) => {
		let index = title.indexOf("[");
		return index !== -1 ? title.substring(0, index) : title;
	}

	getTime = (title) => {
		const start = title.indexOf("[") + 1;
		const end = title.indexOf("]") - 1;
		const time = title.substring(start, end);
		return time.trim() !== "" ? time : null;
	}


	render() {
		const {content, index} = this.props;
		const time = this.getTime(content.title);

		return (
			<QueueAnim type="bottom">
				<div className="post" key={`post-${index}-anime`}>
					{!time ?
					<Link to={`/anime/${content.id}`}>
						<div className="post-thumbnail">
							{ (content.images && content.images[0]) && 
								<img src={`${content.images[0].url}`} alt=""/>
							}
						</div>
					</Link> :
						<div className="post-thumbnail">
							{ (content.images && content.images[0]) && 
								<img src={`${content.images[0].url}`} alt=""/>
							}
							{time && <CountDown time={time}/>}
						</div>
					}
					<div className="post-desc">
						<div className="post-title">
							{this.getTitle(content.title)}
						</div>
						<div className="post-footer">
							
						</div>
					</div>
				</div>
			</QueueAnim>
		);
	}
}



