import { observable, action} from 'mobx';
import api from "../api";

class Store {
	@observable isLoading = false;
    @observable posts = [];
    @observable post = {};
    @observable errors = null;
    @observable nextPageToken = "";
    @observable maxResult = 10;
   
    @action loadPosts() {
    	this.isLoading = true;
    	return api.Posts.fetchPosts(this.maxResult).then(action((res) => {
    		this.posts = res.items;
            this.nextPageToken = res.nextPageToken;
    	})).catch(action((e) => {
    		this.errors = e;
    	})).finally(action(() => {
            setTimeout(() => {
                this.isLoading = false;     
            }, 1000); 
        }));
    }

    @action loadMorePosts() {
        // this.isLoading = true;
        return api.Posts.fetchMorePosts(this.maxResult, this.nextPageToken)
        .then(action((res) => {
            this.posts.replace(this.posts.concat(res.items));    
            this.nextPageToken = res.nextPageToken;
        })).catch(action((e) => {
            this.errors = e;
        })).finally(action(() => {
        }));
    }

    @action loadPost(id) {
        this.isLoading = true;             
        return api.Posts.fetchPost(id).then(action((post) => {
            this.post = post;
        })).catch(action((e) => {
            this.errors = e;
        })).finally(action(() => {
            setTimeout(() => {
                this.isLoading = false;     
            }, 1000);
        }));
    }
}


const store = new Store();

export default store;


