import { client } from "./client";
const url = {
	// posts: "/posts.json",
	// post: "/post.json"
	posts: "/posts",
	post: "/posts"
};
export default {
	fetchPosts: (limit) => {
		return client.get(url["posts"], {
			params: {
				maxResults: limit,
				fetchImages: true,
				fetchBodies: false
			}
		})
		.then((res) => {
			return res.data;
		})
		.catch((e) => {
			return e;
		});
	},

	fetchMorePosts: (limit, nextPageToken) => {
		return client.get(url["posts"], {
			params: {
				maxResults: limit,
				pageToken: nextPageToken,
				fetchImages: true,
				fetchBodies: false
			}
		}).then((res) => {
			return res.data;
		}).catch((e) => {
			return e;
		});
	},

	fetchPost: (id) => {
		return client.get(url["post"], {
			params: {
				fetchImages: true,
				fetchBodies: true
			}
		})
		.then((res) => {
			const sj = res.data.content.replace(/(?:\r\n|\r|\n)/g, '');
			const content = JSON.parse(sj);
			res.data.custom = {...content};
			return res.data;
		})
		.catch((e) => {
			return e;
		});
	}
}


