import React, { Component } from 'react';
import "./style.scss";
export default class index extends Component {
	render() {
		const {id} = this.props;
		const url = `https://drive.google.com/file/d/${id}/preview`;
		return (
			<div className="video">
				<iframe title={id} src={url} width="100%" height="480px"></iframe>
			</div>
		);
	}
}



